@import url('https://fonts.googleapis.com/css2?family=Rubik:ital@0;1&display=swap');

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: transparent !important;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.Actions {
    background-color: rgb(141, 118, 243);
    color: rgb(255, 255, 255);
    font-size: 1.2em;
    margin: 0.3rem;
    width: 50%;
    border-radius: 0.2rem;
    border: 0.5px solid rgb(250, 250, 250);
    text-align: center;
    font-weight: 500;
    font-family: 'Rubik', sans-serif;
}
.Value {
    font-size: 0.6em;
    color: rgb(225, 255, 0);
}

.Value i {
    color: rgb(248, 248, 248);
}
.Actions:hover {
    cursor: pointer;
    color: rgb(225, 255, 0);
    box-shadow: 0.1rem 0.1rem 0.5rem rgba(240, 237, 237, 0.747);
}

.Title {
    background-color: rgb(32, 0, 175);
    color: rgb(232, 241, 232);
    font-size: 0.8em;
    width: 50%;
    border-radius: 0.2rem;
    border: 0.5px solid rgb(238, 238, 238);
    text-align: center;
    font-weight: 600;
    font-family: 'Rubik', sans-serif;
    padding: 0.3rem;
    font-style: italic;
}
